:root {
  --purple: rgb(61, 54, 92);
  --pink: rgb(201, 87, 146);
  --gold: rgb(223, 154, 62);
  --blue-bg: rgb(219, 240, 246);
}


/* Main Styles */
body {
  background-color: rgb(251, 251, 255);
  font-family: Arial, Helvetica, sans-serif;
  color: var(--purple);
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  gap: 30px;
}

.buttonContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.genButton {
  background-color: var(--pink);
  color: white;
  border: none;
  border-radius:5px;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

.genButton:hover {
  background-color: var(--gold);
  color: white;
  transition: 0.6s;
}

/* Header */
.header {
  display: flex;
  width: 100%;
  font-size: 20px;
  gap: 20px;
}

.nav {
  display: flex;
  margin-top: 20px;
}

.nav a {
  color:var(--purple);
  text-decoration: none;
}

.nav a:visited {
  color: var(--purple);
}

.nav a:hover {
  color: var(--pink);
}

.navList {
  list-style: none;
  flex-direction: row;
  display: flex;
  align-items: center;
  gap: 30px;
}

.contactLink {
  background-color: var(--pink);
  color: white !important; 
  border: none;
  padding: 10px 20px;
  border-radius: 6px;
  font-size: 20px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.contactLink:hover {
  background-color: var(--gold);
  color: white;
}

/* Hamburger Menu (Mobile)*/
.hamburger {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 35px;
  height: 20px;
  background: none;
  border: none;
  cursor: pointer;
}

.bar {
  height: 3px;
  width: 100%;
  background-color: var(--pink);
  border-radius: 2px;
}

/*Header Mobile Adjustments*/
@media (max-width: 768px) {
  .hamburger {
    display: flex;
  }

  .navList {
    position: absolute;
    flex-direction: column;
    gap: 20px;
    background: white;
    width: 200px;
    padding: 20px;
    box-shadow: 0px 5px 10px rgba(0,0,0,0.1);
    display: none; 
  }

  .navList.open {
    display: flex;
  }
}

/* Footer */
.footer {
  display: flex;
  width: 100%;
  color: var(--purple);
  font-size: 16px;  
  margin-top: 60px;
  justify-content: center;
}

.socialIcon {
  height: 50px;
  width: 50px;
  margin: 0 15px;
  opacity: 0.7;
}

.attribution a {
  display: flex;
  justify-content: center;
  font-size: 10px;
  color:var(--gold);
  text-decoration: none;
}

.attribution a:visited {
  color:var(--pink);
}

.attribution a:hover {
  color:rgb(61, 54, 92);
  font-size: 14px;
  transition: 1s;
}

/* Images */
.imageContainer {
  display: flex;
}

#introPic {
  border-radius: 100%;
  height: 300px;
  object-fit: cover;
}

.awsBadge {
  height: 200px;
}

.projectImage {
  height: 300px;
  border-radius: 15px;
  object-fit: contain;
}

/* Home */
.home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.home h1 {
  color: var(--purple);
  margin: 20px;
}

.home h2 {
  color: var(--purple);
  margin: 0;
  font-size: 20px;
}

.home h4 {
  color:var(--pink);
  margin: 8px;
  font-size: 16px;
  font-weight: 400;
}

.introBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.textBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 50%;
}

.textBox p {
  font-size: 18px;
  padding: 15px;
}


.introLine {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.scroller {
  max-width: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.scroller[data-animated="true"] {
  overflow: hidden;
  -webkit-mask: linear-gradient(90deg, transparent, white 20%, white 80%, transparent);
  mask: linear-gradient(90deg, transparent, white 20%, white 80%, transparent);
}

.scroller[data-animated="true"] .scrollerBanner {
  flex-wrap: nowrap;
  animation: scroll 20s linear infinite;
}

@keyframes scroll {
  from {
    transform: translateX(0); 
  }
  to {
    transform: translateX(-50%); 
  }
}

.scrollerBanner {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  width: max-content;
  padding-block: 20px;
}

.scrollerBanner img {
  height: 100px;
  width: 100px;
  flex-shrink: 0;
}

/* Projects */

.projectDisplay {
  display: flex;
  flex-direction: column;
}

.projectContainer {
  display: flex;
  flex-direction: column;
  width: 80%;
}

#projectLink {
  margin-bottom: 20px;
  height: 40px;
  width: 160px;
}

/* About */
.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100%;
}

.aboutText {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.techSkillList {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.techSkillList ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  padding: 0;
}

.techSkillList li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  list-style-type: none;
  font-size: 18px;
  color: var(--purple);
  gap: 10px;
}

.addSkills {
  display: flex;
  flex-direction: row;
  gap: 20px;
  justify-content: center;
}

.addSkills ul {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 5px;
}

.addSkills li {
  font-size: 16px;
}

.certList {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.certList ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  padding: 0;
}

.certList li {
  list-style: none;
}

.icon {
  width: 100px;
  height: 100px;
}

.resumeLink {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin-top: 30px;
  margin-bottom: 50px;
  justify-content: center;
  align-items: center;
}

.resumeLink p {
  text-align: center;
  font-size: 16px;
}

.interestsList {
  display: flex;
  flex-direction: column;
  width: 80%;
}

.interestsList ul {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.interestsAlt {
  background-color: var(--blue-bg);
  width: 100%;
}

.interestsList li {
  display: flex;
  flex-direction: row;
  list-style: none;
  padding: 10px;
  align-items: center;
}

.interestsList img {
  margin-right: 15px;
  width: 50px;
  height: 50px;
}

/* Contact Page */
.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contactForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contactLabel {
  color: rgb(61, 54, 92);
  font-weight:500;
  font-size: 18px;
  border-radius: 5px;
  line-height: 22px;
  background-color: transparent;
  border: 1px solid var(--pink);
  padding: 13px;
  margin-bottom: 15px;
  width:100%;
}

.contactForm textarea {
  height: 150px;
  line-height: 150%;
  resize:vertical;
}

.contactForm button {
  width: 200px;
  background: var(--pink);
  border-radius:5px;
  border:0;
  cursor:pointer;
  color:white;
  font-size:24px;
  padding-top:10px;
  padding-bottom:10px;
}

.contactForm button:hover {
  background-color: var(--gold);
  color: white;
  transition: 0.6s;
}



/* Effects */
.hoverGrow {
  transition: transform 0.7s ease;
}

.hoverGrow:hover {
  transform: scale(1.2);
}

/*About Mobile adjustments */
@media (max-width: 768px) {
  .aboutText h2,
  .aboutText h3 {
    text-align: center;
  }

  .hoverGrow:hover {
    transform: none;
  }

  .techSkillList ul {
    gap: 15px;
  }

  .techSkillList li {
    padding: 10px;
    font-size: 16px;
  }

  .icon {
    width: 70px;
    height: 70px;
  }

  .addSkills {
    flex-direction: column; 
    gap: 20px;
  }

  .certList ul {
    justify-content: center;
    gap: 20px;
  }

  .interestsList {
    width: 100%; 
    padding: 0 15px;
  }

  .interestsList li {
    font-size: 14px;
  }
}